.Headline {
    // margin-top: 1.5em;
    display: flex;  
    // max-width:35ch;

    margin-left:7vmax;
    margin-right:7vmax;
    margin-bottom: 2rem;
    margin-top: 2rem;
    font-size:1.5rem;
    transition: width 2s, height 2s, background-color 2s, transform 2s;
}

.Headline :nth-child(2) {
    border-radius:0;
}

.Headline :nth-child(1) {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.Headline :nth-child(3) {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}


.Headline div {
    // margin-right: 1.5em;
    display: flex;  
    justify-content: space-around;
    align-items: center;
    width:100%;
    background-color: black;
    color: white;
    border-radius: 3em;
    padding: 0 .5em 0 .5em;
    
}

.Headline :last-child {
    margin-right:0;
}

.Headline div {
    transition: all 0.35s;
}

.Headline :hover {
    box-shadow: 3px 7px 5px gray;
    text-shadow: 2px 0px 2px white;
    font-weight: bolder;
    
}