$fontname: './fonts/JetBrainsMono-VariableFont_wght.ttf';
$fontnameitalic: './fonts/JetBrainsMono-Italic-VariableFont_wght.ttf';

@font-face {
  font-family: 'JetBrains';
  src: url($fontname) format('opentype');
}

@font-face {
  font-family: 'JetBrains-Italic';
  src: url($fontnameitalic) format('opentype');
}

body {
  overflow-x: hidden;
}

.App {
  // text-align: center;
  font-family: JetBrains;
  // margin-left: 15vmin;
  // margin-right: 15vmin;
  
}

.AppContainer {
  position: relative;
  transform: translateX(50%);
}

.AppShiftContainer {
  min-width:1em;
  max-width:35em;
  transform: translateX(-50%);
}



.AppCursor {
  animation: App-cursor-blink infinite 1s step-end;
  display: inline-block;
}

.AppText {
  text-align: left;
  margin-right: 1vmax;
  white-space: nowrap;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 55s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


@keyframes App-cursor-blink {
  0% {
    opacity: 0%;
  }
  50% {
    opacity: 100%;
  }
  100% {
    opacity: 100%;
  }
}
