$hexw : 8ch;
$hmod : 1.73;
$mmod : 3.46;
$bmod : 2;

.hexagon {
    position: relative;
    width: $hexw; 
    height: $hexw / $hmod;
    background-color: black;
    margin-top: $hexw / $bmod;

    animation: infinite 5s backshine linear;
  }
  
  .hexagon:before,
  .hexagon:after {
    content: "";
    position: absolute;
    width: 0;
    border-left: $hexw / $bmod solid transparent;
    border-right: $hexw / $bmod solid transparent;
  }
  
  .hexagon:before {
    bottom: 100%;
    border-bottom: $hexw / $mmod solid black;
  }
  
  .hexagon:after {
    top: 100%;
    width: 0;
    border-top: $hexw / $mmod solid black;
  }



  .hexagonEmpty {
    position: relative;
    width: $hexw; 
    height: $hexw / $hmod;
    background-color: transparent;
    margin-top: $hexw / $bmod;
  }
  
  .hexagonEmpty:before,
  .hexagonEmpty:after {
    content: "";
    position: absolute;
    width: 0;
    border-left: $hexw / $bmod solid transparent;
    border-right: $hexw / $bmod solid transparent;
  }
  
  .hexagonEmpty:before {
    bottom: 100%;
    border-bottom: $hexw / $mmod solid transparent;
  }
  
  .hexagonEmpty:after {
    top: 100%;
    width: 0;
    border-top: $hexw / $mmod solid transparent;
  }

  .CursorContainerCenter {
    position: absolute;
    left:50%;
    display: flex;
    bottom: 0px;
    z-index: 999;
  }
  
  .CursorContainer {
    // opacity: 35%;
    left:-50%;
    position: relative;
    font-size: 1.35rem;
    pointer-events: none;
    white-space: nowrap;
    margin-bottom:.41em;
  }
  
  .AppTyping {
    display: inline-block;
    animation: App-typing 1 5s linear;
    overflow-x: hidden;
    letter-spacing: .75ch;
    // margin: 10ch 0 0 10ch;
    max-width: 0;
    white-space: nowrap;
    font-family: monospace;
    background-color: black;
  }


.AppCursorComb {
    animation: cursor-blink-comb infinite 1s step-end;
    display: inline-flex;
    color:black;
  }
  
  @keyframes App-typing {
    0% {
      max-width: 0;
      color:white;
    }
    50% {
      max-width: 100%;
    }
    70% {
      max-width: 100%;
    }
    100% {
      max-width: 0;
      color:white;
    }
  }

  @keyframes cursor-blink-comb {
    0% {
      opacity: 0.0;
    }
    50% {
      opacity: 1.0;
    }
    100% {
      opacity: 0.0;
    }
  }