@import '~material-design-icons/iconfont/material-icons.css';

.PostContainer {
    text-align: left;
    margin-left:.35rem;
    margin-right:.35rem;
}

.PostHeader {
    font-family: JetBrains-Italic;
    font-size: 1.1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: solid 1px black;
}

.PostHeader p {
    margin-bottom: 0px;
}


.PostHeader :nth-child(1){
    font-weight: bolder;
}

.PostBody {
    font-size: 1rem;
    margin-bottom: 1vmin;
    margin-left: 3ch;
}


.PostBody ::first-line {
    text-indent: 3ch;
    margin-left:3ch;
}


.PostFooter {
    display: flex;
    justify-content: space-between;
    align-content: center;
}

.PostSubFooter {
    display: flex;
    justify-content: space-around;
}

.PostFooter span {
    cursor:pointer;
}